.navbar {
  height: 7vh;
  padding: 0px 15% 0px 10%;
  color: white;
  display: flex;
  margin-bottom: 5vh;
  z-index: 1;
 /*  justify-content: space-between; */
  background-color: rgba(40, 40, 40, 0.5);
  backdrop-filter: blur(5px);
}

.navbar span {
  margin: auto 0;
  box-sizing: border-box;
  font-size: 28px;
}

.navbar-contact {
  color: var(--main-color);
  padding: 0 1rem;
  margin: auto 0;
  background: none;
  outline: none;
  border: none;
  font-size: 22px;
  border-radius: 15px;
  border: 2px solid var(--main-color);
  box-sizing: border-box;
  font-family: inherit;
  font-weight: 400;
  cursor: pointer;
  justify-self: end;
  align-self: end;
  transition: all 0.3s;
}

.navbar-contact:hover {
  background-color: var(--main-color);
  color: black;
  transition: all 0.3s;
}

.navbar-logo {
  cursor: pointer;
  user-select: none;
}

.navbar-links {
  margin: auto 0 auto auto;
  display: flex;
}

.navbar a {
  display: flex;
  margin: auto;
}

.contact-link {
  height: 32px;
  filter: saturate(0);
}

.contact-link:hover {
  filter: saturate(1);
  transition-duration: 200ms;
}

#github {
  margin: auto 0 auto auto;
  padding: 0.3rem;
  box-sizing: border-box;
}


#github:hover {
  animation: rotate 300ms ease-in;
}

@keyframes rotate {
  0% {
      transform: rotate(0deg);
  }
  50% {
      transform: rotate(40deg);
  }
  100% {
      transform: rotate(0deg);
  }
}
#linkedin {
  margin: auto;
}

.main {
  width: 100%;
  margin-top: 10vh
  /* background-color: rgb(29, 29, 29) */;
  display: flex;
  flex-direction: column;
  z-index: 1;
  pointer-events:none;
}

.greeting {
  margin: 5% auto;
  color: white;
  display: flex;
  flex-direction: column;
  /* background: linear-gradient(135deg, rgba(255, 255, 255, 0.078), rgba(255, 255, 255, 0)) ;   
  backdrop-filter: blur(5px); */
  border-radius: 10px;
  padding-bottom: 1rem;
  /* border: 1px solid rgba(255, 255, 255, 0.096) */
  /* box-shadow: 5px 5px 15px 5px #adadad; */
  /* outline: 1px solid white */;
}


.greeting .title {
  font-size: 6.5rem;
  background: -webkit-linear-gradient(-90deg,rgb(255, 255, 255), rgb(207, 207, 207));
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  
}

.greeting .details {
  color: rgb(189, 189, 189);
  padding: 0 10%;
}





.svg-blur {
  visibility: hidden;
  height: 0;
  width: 0;
}

.control-panel {
  position: relative;
  background-color: rgba(50, 50, 50, 0.5);
  /* width: 30%; */
  border-radius: 8px;
  /* background-color: rgb(255, 255, 255); */
  margin: auto auto 2rem auto;
  display: flex;
  box-sizing: content-box;
  align-items: center;
  backdrop-filter: blur(5px);
  outline: 1px solid rgb(255, 255, 255);
  animation-iteration-count: 1;
  overflow: hidden;
}
.control-panel a {
  animation: panel-appear 1.5s ease-out;
  padding: 0.4rem 2rem; 
  margin: 0;
  color: white;
  text-decoration: none;
 /*  width: 50%; */
  height: 100%;
  display: flex;
  transition-duration: 200ms;
}

/* .control-panel a:first-of-type {
  border-radius: 20px 0 0 20px;
}
.control-panel a:last-of-type {
  border-radius: 0 20px 20px 0;
} */

.link-active {
  background:white;
  color: white;
}

.link-active span {
  color: black;
}

@keyframes panel-appear {
  0% {
    padding: 0.4rem 0rem;
    opacity: 0;
  }

  40% {
    padding: 0.4rem 0rem;
  }

  60% {
    opacity: 0;
  }

  100%{
    padding: 0.4rem 2rem;
    opacity: 1;
  }
}

.control-text {
  margin: auto;
  font-size: 1.2rem;
  cursor: pointer;
  user-select: none;
  /* animation: control-text-appear 1.5s ease-out; */
  animation-iteration-count: 1;
}


@keyframes control-text-appear {
  0% {
    opacity: 0;
  }

  75% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}


@media screen and (max-width: 500px) {
  .App {
    justify-content: space-between;
  }

  .main {
    margin-bottom: 3rem;
  }

  .navbar {
    padding-right: 0;
  }

  .navbar-contact {
    height: 46px;
    font-size: 1.2rem;
    padding: 0 0.5rem;
    border-radius: 20px;
  }

  .control-panel{
    width: 100%;
  }

  .greeting .title {
    font-size: 4rem;
  }

  .greeting .details {
    font-size: 1rem;
  }

  @keyframes panel-appear {
    0% {
      width: 5%;
      opacity: 0;
    }
  
    35% {
      width: 5%;
      opacity: 0.6;
    }
  
    100%{
      width: 100%;
      opacity: 1;
    }
  }
}
